@import url(https://fonts.googleapis.com/css?family=Playfair+Display:400,700|Raleway:400,600);
body {
  margin: 0;
  padding: 0;
}

*{
  margin: 0;
  padding: 0;
}

.grid{
  padding: 0 197px;
  display: grid;
  grid-template-columns: repeat(12, 1fr);
  grid-gap: 10px;
  grid-row-gap: 10px;
}

h1, h2{
  font-family: 'Playfair Display', serif;
  font-weight: 500;
}

p, a, small{
  font-family: 'Raleway', sans-serif;
}

p{
  font-size: 20px;
}

h1{
  font-size: 48px;
}

h2{
  font-size: 40px;
  margin-bottom: 15px;
}

.intro{
  background-image: url(/portfolio/static/media/background-intro.f3cf96b9.jpg);
  -webkit-clip-path: polygon(0px 100%,100% 80%,100% 0px,0px 0px);
  -ms-clip-path: polygon(0px 100%,100% 80%,100% 0px,0px 0px);
  clip-path: polygon(0px 100%,100% 80%,100% 0px,0px 0px);
  background-position: center;
  height: 660px;
  background-size: cover;
  grid-column: 1 / span 11;
}

.intro-content{
  grid-column: 7 / span 6;
  padding: 210px 0;
}

.typewrite::after{
  content: "|";
  margin-left: 5px;
  opacity: 1;
  -webkit-animation: t-writer 1s infinite;
          animation: t-writer 1s infinite;
}

.about{
  padding: 20px 197px;
}

.skills{
  padding: 140px 197px 80px 197px;
}

.portfolio, .contact{
  padding: 80px 197px;
}

.about p{
  padding: 0 120px;
}

.skills-content, .works-options, .contact div{
  display: flex;
  justify-content: space-around;
}

.skills{
  -webkit-clip-path: polygon(0px 100%,100% 100%,100% 0px,0px 20%);
  -ms-clip-path: polygon(0px 100%,100% 100%,100% 0px,0px 20%);
  clip-path: polygon(0px 100%,100% 100%,100% 0px,0px 20%);
  background-color: #F5EFE3;
  line-height: 1.6;
}

.sub-skill{
  margin-left: 20px;
}

.skills small{
  font-size: 16px;
  display: block;
}

.mockup{
  width: 50%;
  display: block;
  margin: 0 auto;
  position: relative;
}

.portfolio{
  position: relative;
}

.image{
  width: 28%;
  left: 36%;
  z-index: -1;
  top: 38%;
  position: absolute;
  margin-right: -35px;
  margin-top: -35px;
}

.contact{
  background-color: #E6E7E5;
  text-align: center;
}

.contact div{
  padding-top: 60px;
}

.contact a{
  display: block;
  color: #000;
  font-weight: 700;
  text-decoration: none;
  font-size: 14px;
  margin-top: 10px;
  text-transform: uppercase;
  letter-spacing: 2px;
}

.contact a:hover{
  color: #a6aa9c;
}

footer{
  background-color: #CCCCCC;
  text-align: center;
  padding: 5px 0;
  font-size: 12px;
}

footer a{
  color: #000;
  text-decoration: none;
  font-weight: 700;
}

@-webkit-keyframes t-writer {
  0%, 100% {
    opacity: 1;
  }
  50% {
    opacity: 0;
  }
}

@keyframes t-writer {
  0%, 100% {
    opacity: 1;
  }
  50% {
    opacity: 0;
  }
}

@media only screen and (min-device-width : 768px) and (max-device-width : 1024px) {
  .intro-content {
    padding: 100px 0;
    grid-column: 3 / span 10;
  }
  .h1-intro {
    font-size: 44px;
  }
  .typewrite {
    font-size: 36px;
  }
  .mockup {
    left: 25%;
  }
  .image {
    left: 30%;
  }
}

@media only screen and (min-device-width : 0) and (max-device-width : 560px) {
  h1{
    font-size: 38px;
  }
  h2{
    font-size: 30px;
  }
  #intro, #about, #portfolio, #contact {
    padding: 0;
  }
  .about p{
    padding: 0 60px;
  }
  .intro {
    background-image: url(/portfolio/static/media/background-mobile.4752ab61.jpg);
    -webkit-clip-path: polygon(0px 100%,100% 90%,100% 0px,0px 0px);
    -ms-clip-path: polygon(0px 100%,100% 90%,100% 0px,0px 0px);
    clip-path: polygon(0px 100%,100% 90%,100% 0px,0px 0px);
  }
  .intro-content {
    padding: 40px 0;
    grid-column: 2 / span 12;
  }
  .h1-intro {
    font-size: 38px;
  }
  .typewrite {
    font-size: 30px;
  }
  #portfolio, #contact {
    padding: 40px 0;
  }
  #skills {
    padding: 100px 0;
    text-align: center;
    -webkit-clip-path: polygon(0px 100%,100% 100%,100% 0px,0px 10%);
    -ms-clip-path: polygon(0px 100%,100% 100%,100% 0px,0px 10%);
    clip-path: polygon(0px 100%,100% 100%,100% 0px,0px 10%);
  }
  nav {
    grid-column: 1 / span 12 !important;
    margin: 0 auto;
  }
  .mockup {
    width: 80%;
  }
  .image {
    width: 62%;
    left: 18.5%;
    top: 28.6%;
  }
  .works-options, #contact div, .skills-content {
    display: flex;
    flex-direction: column;
  }
  .works:first-child {
    margin-top: 25px !important;
  }
  .works {
    margin-top: 0 !important;
  }
  #contact div {
    padding-top: 30px;
  }
  #contact p {
    margin-bottom: 30px;
  }
}
nav{
  padding-top: 30px;
  grid-column: 8 / span 5;
}

.nav-item{
  font-family: 'Raleway', sans-serif;
  text-transform: uppercase;
  text-decoration: none;
  letter-spacing: 2px;
  color: #000;
  font-size: 14px;
  padding: 10px 15px;
}
  
.nav-item:hover{
  color: #a6aa9c;
  font-weight: 700;
}
.works{
    margin-top: 40px;
    background-color: #fff;
    padding: 10px 20px;
    border: none;
    text-align: center;
    font-size: 14px;
    cursor: pointer;
    letter-spacing: 2px;
    border-bottom: 1px solid #fff;
  }
  
  .works:hover{
    border-bottom: 1px solid #DED8C3;
    font-weight: 700;
  }
.h1-content{
    padding-bottom: 40px;
    text-align: center;
    grid-column: 1 / span 12;
  }
